import { Modal, Input } from "antd";
import { Formik } from "formik";
import {PaystackButton } from 'react-paystack';
import { dataType, ProductType } from "../../../shared/types";
import { FlatButton } from "../../../shared/FlatButton";
const styles={
    logo:{
            width: '55px',
            height: '50px',
        },
}
type modaltype = {
    isOpen:boolean,
    selectedService:ProductType | null
    handleCloseModal:()=>void,
    loading:boolean, 
    setDataSubmit:React.Dispatch<React.SetStateAction<dataType>>,
    setProceedPayment:React.Dispatch<React.SetStateAction<boolean>>,
    proceedPayment:boolean,
    componentProp: {
    email: string;
    amount: number;
    metadata: {
        custom_fields: {
            display_name: string;
            variable_name: string;
            value: string | undefined;
        }[];
    };
    publicKey: string;
    text: string;
    onSuccess: () => void;
    onClose: () => void;
}


}

export const ModalComponent = ({componentProp, isOpen, selectedService, handleCloseModal, loading, setDataSubmit, setProceedPayment, proceedPayment}:modaltype)=>{
    return (
        <Modal
            open={isOpen}
            title={selectedService?.title}
            onCancel={handleCloseModal}
            footer={null}
            >
            <p>{selectedService?.description}</p>

            <Formik
            initialValues={{
                name: '',
                phone: '',
                email: '',
                address: '',
                message: '',
                date: null,
                service: selectedService?.title || '',
            }}
            onSubmit={(values) => {
                console.log(values);
                setDataSubmit(values);
                setProceedPayment(true); // Call your backend handler here
            }}
            >
            {(formik) => (
                <form onSubmit={formik.handleSubmit}>
                <Input
                    name="name"
                    value={formik.values.name}
                    onChange={formik.handleChange}
                    placeholder="Full Name"
                    style={{ marginBottom: '1rem' }}
                    required
                />

                <Input
                    name="phone"
                    value={formik.values.phone}
                    onChange={formik.handleChange}
                    placeholder="Phone Number"
                    style={{ marginBottom: '1rem' }}
                    required
                />

                <Input
                    name="email"
                    value={formik.values.email}
                    onChange={formik.handleChange}
                    placeholder="Email (optional)"
                    style={{ marginBottom: '1rem' }}
                />

                <Input
                    name="address"
                    value={formik.values.address}
                    onChange={formik.handleChange}
                    placeholder="Delivery Address"
                    style={{ marginBottom: '1rem' }}
                    required
                />



                <Input.TextArea
                    name="message"
                    value={formik.values.message}
                    onChange={formik.handleChange}
                    placeholder="Additional Notes"
                    rows={3}
                    style={{ marginBottom: '1rem' }}
                />

                {/* <DatePicker
                    name="date"
                    style={{ width: '100%', marginBottom: '1rem' }}
                    placeholder="Preferred Date (optional)"
                    onChange={(date, dateString) =>
                    formik.setFieldValue('date', dateString)
                    }
                /> */}

                {/* Hidden Service Title Field */}
                <input
                    type="hidden"
                    name="service"
                    value={formik.values.service}
                />

                {
                    !proceedPayment ? <FlatButton title="submit" className="btndark"/>:
                        <>
                        <small>You will be redirected to PayStack to make payments securely**</small>
                        <PaystackButton disabled={loading} className="btn btn-block w-100 btn-primary " {...componentProp} />
                        <div>
                            <img style={styles.logo} src="data:image/jpeg;base64," alt='visa'/>
                            <img style={styles.logo} src="data:image/png;base64," alt="paystack" />
                            <img style={styles.logo} src='data:image/png;base64,'alt="master card"/>
                        </div>

                    </>
                }
                </form>
            )}
            </Formik>
        </Modal>
    )
}